:root{
    --border: #0A285F
}

.card{
    margin: 0 20px;
    border: 10px solid var(--border);
    background-color: #FFCC00;
    border-radius: 15px;
    color: #000;
    padding: 20px 20px;
    box-shadow: -10px 10px 0px black;
    text-align: center;
}

img {
    max-width: 1%;
    max-height: 1%;
}

.card-title{
    height: 100px;
    background: url('/src/images/pokemon.png') no-repeat center;
    background-size: 200px 100%;
    border-bottom: 2px solid var(--border);
}

.card-content{
    margin: 10px auto;
    width: 200px;
}

.buttons{
    margin-top: 20px;
    display: flex;
    gap: 1rem;
    justify-content: center;
    
}

button{
    background: #D5A100;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid var(--border);
    border-radius: 8px;
    width: 100px;
    height: 40px;
    cursor: pointer;
    transition: .5s;
    box-shadow: -1px 1px 0px black;
}

button:hover {
    background-color: var(--border);
    color: #FFCC00;
    transform: scale(0.98);
    opacity: 0.9;
    box-shadow: -2px 2px 0px black;
}